
body {
  height: 125vh;
  
  background-size: cover;
  font-family: sans-serif;
  margin-top: 30px;
  padding: 30px;
  
  align-items: center;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
}

main {
  color: white;
}

header {
  background-color: white;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 30px;
  display: block;
  align-items: flex-end;
  box-shadow: 0 0 25px 0 black;
}
content {
  margin-top: 100px;
}

header * {
  display: inline;
}

header li {
  
  
  display: inline-block;
  width: 0px;
     height: 30px;
     
     
     margin-left: 150px;
     padding-left: 20px;
     
  
}

header li a {
  color: black;
  text-decoration: none;
}

hr.new1 {
  border-top: 1px solid black;
}

a:link {
  text-decoration: none;
}

